/**
 * Memory configuration utilities
 */
export interface MemoryConfig {
  disabled?: boolean;
  personalize?: boolean;
  [key: string]: unknown;
}

/**
 * Checks if memory is enabled based on the configuration
 * @param memoryConfig - Memory configuration object
 * @returns boolean indicating if memory is enabled
 */
export function isMemoryEnabled(memoryConfig?: MemoryConfig): boolean {
  if (!memoryConfig) {
    return false;
  }
  
  // If explicitly disabled, return false
  if (memoryConfig.disabled === true) {
    return false;
  }
  
  // If there's a valid memory config object and not explicitly disabled, consider it enabled
  return true;
}
